import { Value } from '@ali/4ever-cangjie';
import InlineCode from '../../mo/models';
export default function isCursorInInlineCode(value: Value): boolean {
  const { selection } = value;
  if (!selection || selection.isExpanded) return false;
  // 选区闭合，获取anchor即可
  const inline = value.anchorInline;
  return !!InlineCode.isInline(inline);
}
